<template>
    <v-container id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/mutstokperkiraancpkb" link>Back</v-btn>
        <template id="template">
            <table>
                <tr>
                    <td style="padding-bottom: 40px;">
                        <table width="396px">
                            <tr>
                                <td><img style="vertical-align: middle;" src="../../assets/logoNiagara.png" ></td>
                                <td colspan="2" style="text-align: center;"><b>KARTU PENIMBANGAN</b></td>
                            </tr>
                            <tr>
                                <td>Kode produk</td>
                                <td>:</td>
                                <td id="btm">{{this.induk["Kode Barang Jadi"]}}</td>
                            </tr>
                            <tr>
                                <td>Nama Produk</td>
                                <td>:</td>
                                <td id="btm">{{induk["Nama Barang"]}}</td>
                            </tr>
                            <tr>
                                <td>No. Bets</td>
                                <td>:</td>
                                <td id="btm">{{induk["No LP"]}}</td>
                            </tr>
                            <tr>
                                <td>Tgl Produksi</td>
                                <td>:</td>
                                <td id="btm">{{induk["TanggalFormat"]}}</td>
                            </tr>
                        </table><br>
                        <table id="tabel">
                            <tr>
                                <td id="td" style="text-align: center;font-size: 10pt" width="90px">Nama Bahan</td>
                                <td id="td" style="text-align: center;font-size: 10pt" width="66px">No LP</td>
                                <td id="td" style="text-align: center;font-size: 10pt" width="66px">Formulasi (gr)</td>
                                <td id="td" style="text-align: center;font-size: 10pt" width="50px">Hasil Timbang</td>
                                <td id="td" style="text-align: center;font-size: 10pt" width="50px">Ditimbang Oleh</td>
                                <td id="td" style="text-align: center;font-size: 10pt" width="50px">Diperiksa Oleh</td>
                            </tr>
                            <tr v-for="item in anak" :key='item.id'>
                                <td id="td" style="font-size: 9pt">{{item["Nama Barang"]}}</td>
                                <td id="td" style="text-align: center;font-size: 9pt">{{item["No LP"]}}</td>
                                <td id="td" style="text-align: center;font-size: 9pt">{{item["Keluar"]}}</td>
                                <td id="td" style="text-align: center;font-size: 9pt"></td>
                                <td id="td" style="text-align: center;font-size: 9pt"></td>
                                <td id="td" style="text-align: center;font-size: 9pt"></td>
                            </tr>
                        </table>
                    </td>
                </tr>
            </table>
        </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                
                induk: [],
                anak: [],
               
            }
        },
        methods: {
            getData() {
               var uri = this.$apiUrl + 'mutstokcpkbperkiraans/header/' + this.$route.params.kode
                this.$http.get(uri).then(response => {
                    this.induk = response.data[0]
                    this.induk["Kode Barang Jadi"] = this.induk["Kode Barang"].slice(0,2) + this.induk["Kode Barang"].slice(4,6)
                })
            },

            getDataAnak() {
               var uri = this.$apiUrl + 'mutstokcpkbperkiraans/detail/' + this.$route.params.kode
                this.$http.get(uri).then(response => {
                    this.anak = response.data
                    console.log(this.anak)
                })
            },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        created() {
            this.getData();
            this.getDataAnak();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
      
    }
    #tabel{
        border-collapse: collapse;
    }
    #tabel #th, #td {
        border: 1px solid black;
    }
    #btm{
        border-bottom: 1px solid black;
        width: 220px;
    }
</style>